<script>
  export default {
    data () {
      return {}
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  @import "../../../../style/project/project.styl"
  .project-dec
    min-height 300px
  .project-dec-content
    display table
    padding 5px
    .project-dec-content-item
      display table-row
      .dec-key
      .dec-value
        display table-cell
        padding-right 10px
        padding-top 5px

</style>



