<template>
  <div class="question-create">
    <Form :model="form" :label-width="80" :rules="ruleValidate">
      <FormItem label="项目" prop="level">
        <Input placeholder=""></Input>
      </FormItem>

      <FormItem label="问题类型" prop="level">
        <Input placeholder=""></Input>
      </FormItem>

      <FormItem label="主题" prop="level">
        <Input placeholder="此处填写问题的主题,最多不超过80个字符"></Input>
      </FormItem>

      <FormItem label="描述">
        <Ueditor :config="config" ref="ueditor"></Ueditor>
      </FormItem>

      <FormItem label="附件">
        <Button type="primary" icon="upload" size="small">上传</Button>
      </FormItem>

      <Row>
        <Col span="12">
        <FormItem label="优先级">
          <Select v-model="form.level" class="question-create-select" size="large">
            <Option v-for="(item,index) in levelList" :key="index" :value="item.status">
              <Icon :type="item.icon" :color="item.color"></Icon>
              {{item.label}}
            </Option>
          </Select>
        </FormItem>
        </Col>

        <Col span="12">
        <FormItem label="结束时间">
          <DatePicker :value="form.endTime" format="yyyy-MM-dd" type="date" placeholder="Select date"
                      style="width: 100%"></DatePicker>
        </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span="12">
        <FormItem label="模块">
          <Select v-model="form.level" class="question-create-select" size="large">
            <Option v-for="(item,index) in levelList" :key="index" :value="item.status">
              <Icon :type="item.icon" :color="item.color"></Icon>
              {{item.label}}
            </Option>
          </Select>
        </FormItem>
        </Col>

        <Col span="12">
        <FormItem label="指派给">
          <Select v-model="form.level" class="question-create-select" size="large">
            <Option v-for="(item,index) in levelList" :key="index" :value="item.status">
              <Icon :type="item.icon" :color="item.color"></Icon>
              {{item.label}}
            </Option>
          </Select>
        </FormItem>
        </Col>
      </Row>
    </Form>
  </div>
</template>
<script>
  import Ueditor from '../../../common/ueditor/ueditor.vue'

  export default {
    components: {
      Ueditor
    },
    data () {
      return {
        levelList: [
          {
            status: 0,
            label: '低',
            icon: 'record',
            color: '#5bc0de'
          },
          {
            status: 1,
            label: '中',
            icon: 'record',
            color: '#ffbc00'
          },
          {
            status: 2,
            label: '高',
            icon: 'record',
            color: '#45be95'
          }
        ],
        form: {
          level: 1,
          endTime: '2017-01-29'
        },
        ruleValidate: {
          level: [
            {
              required: true
            }
          ]
        },
        config: {
          initialFrameWidth: null,
          initialFrameHeight: 250
        }
      }
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  .question-create
    width 100%
    padding-bottom 100px
    padding-right 30px
    overflow scroll
</style>



