<template>
  <Card class="setting-card">
    <p slot="title">
      <Icon type="document-text"></Icon>
      项目信息
    </p>
    <br>
    <Row type="flex" justify="center">
      <Col span="23">
      <Form :label-width="80">
        <FormItem label="项目名称">
          <Input v-model="question.name"></Input>
        </FormItem>
        <FormItem label="项目颜色">
          <Select v-model="question.color" size="large">
            <Option value="red"><Icon type="record" color="red"></Icon> 红色</Option>
            <Option value="orange"><Icon type="record" color="orange"></Icon> 橙色</Option>
            <Option value="yellow"><Icon type="record" color="yellow"></Icon> 黄色</Option>
            <Option value="green"><Icon type="record" color="green"></Icon> 绿色</Option>
            <Option value="indigo"><Icon type="record" color="indigo"></Icon> 青色</Option>
            <Option value="blue"><Icon type="record" color="blue"></Icon> 蓝色</Option>
            <Option value="purple"><Icon type="record" color="purple"></Icon> 紫色</Option>
          </Select>
        </FormItem>
        <FormItem label="项目描述">
          <Input type="textarea" v-model="question.summary" rows="5"></Input>
        </FormItem>

        <FormItem label="项目pKey">
          <span>{{question.key}}</span>
        </FormItem>

        <FormItem label="">
          <Button type="primary">保存</Button>
        </FormItem>
      </Form>
      </Col>
    </Row>
  </Card>
</template>

<script>
  export default {
    data () {
      return {
        question: {
          name: 'bugfb',
          summary: 'bug管理平台',
          color: 'red',
          key: 'f7f4ddb05b5f5af817a0b04c47394eef'
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">

</style>



