<template>
  <UAffix ref="affix" :isSwitch="uAffixSwitch" @on-change="affixChange">
    <div class="question-detail-container" :class="questionDetailContainerSpread"
         :style="questionDetailContainerStyle">
      <Card class="question-detail-card">
        <div id="question_detail">
          <!-- 基本信息-->
          <div class="question-detail-base">
            <Row type="flex" align="middle">
              <Col span="1">
              <Button icon="chevron-right" type="text"></Button>
              </Col>
              <Col span="2">
              <span>{{question.id}}</span>
              </Col>
              <Col span="21">
              <input v-model="question.title" class="question-title"/>
              </COl>
            </Row>
            <Row type="flex" justify="end">
              <Col span="23" class="question-detail-base-2">
              <span>{{question.author}}</span>
              <span>创建于{{question.createAt}},</span>
              <span>{{question.author}}更新于{{question.updateAt}}</span>
              </Col>
            </Row>
            <Row type="flex" justify="end" class="question-detail-base-3" :gutter="12">
              <Col span="2" style="text-align: right">
              <span>状态:</span>
              </Col>
              <Col span="6">
              <DropSelect :data="statusList" v-model="question.status" :searchSwitch="true"></DropSelect>
              </Col>
              <Col span="2" style="text-align: right">
              <span>优先级:</span>
              </Col>
              <Col span="6">
              <DropSelect :data="levelList" v-model="question.level"></DropSelect>
              </Col>
              <Col span="2" style="text-align: right">
              <span>类型:</span>
              </Col>
              <Col span="6">
              <DropSelect :data="questionCategoryList" v-model="question.category"></DropSelect>
              </Col>
            </Row>
            <hr>
          </div>
          <!--问题描述-->
          <div class="question-summary">
            <p>
              <Icon type="document-text" size="14"></Icon>
              <span>描述</span>
            </p>
            <Sueditor :content="question.summary" :height="50" @content-update="updateQuestionSummary"></Sueditor>
            <hr>
          </div>

          <!--额外信息-->
          <div class="question-extra">
            <Row>
              <Col span="12">
              <Icon type="document-text" size="14"></Icon>
              <span>指派</span>
              <DropSelect :data="statusList" v-model="question.status" :searchSwitch="true"></DropSelect>
              </Col>
              <Col span="12">
              <Icon type="document-text" size="14"></Icon>
              <span>截止</span>
              <DropSelect :data="statusList" v-model="question.status" :searchSwitch="true"></DropSelect>
              </Col>
            </Row>
            <br>
            <Row>
              <Col span="12">
              <Icon type="document-text" size="14"></Icon>
              <span>模块</span>
              <DropSelect :data="statusList" v-model="question.status" :searchSwitch="true"></DropSelect>
              </Col>
              <Col span="12">
              <Icon type="document-text" size="14"></Icon>
              <span>版本</span>
              <DropSelect :data="statusList" v-model="question.status" :searchSwitch="true"></DropSelect>
              </Col>
            </Row>
          </div>
          <hr>
          <!-- 附件-->
          <div class="question-attachment">
            <p>
              <Icon type="paperclip"></Icon>
              <span>附件</span></p>
            <Button type="ghost" size="small" icon="upload">上传</Button>
          </div>
          <hr>
          <!--评论载体 -->
          <CommentList></CommentList>
        </div>
      </Card>
      <CommentSend :left="commentSendSpread" elWidth="60%"></CommentSend>
    </div>
  </UAffix>
</template>
<script>
  import DropSelect from '../../../common/select/drop-select.vue'
  import Sueditor from '../../../common/edit/sueditor/sueditor.vue'
  import CommentList from '../../../common/comment/comment.vue'
  import CommentSend from '../../../common/comment/commentSend.vue'
  import UAffix from '../../../common/UAffix.vue'

  export default {
    props: {
      panelSpread: {
        type: Boolean,
        default: false
      },
      uAffixSwitch: {
        type: Boolean,
        default: false
      }
    },
    components: {
      UAffix,
      DropSelect,
      Sueditor,
      CommentList,
      CommentSend
    },
    created () {
      console.log('created')
      this.$store.dispatch('getAllStatus')
      this.$store.dispatch('getQuestion')
      this.$store.dispatch('getQuestionLevels')
      this.$store.dispatch('getQuestionCategories')
    },
    mounted () {
      this.updateCommentSendSpread(this.panelSpread)
    },
    computed: {
      questionDetailContainerSpread () {
        return this.panelSpread ? 'question-detail-container-spread' : 'question-detail-container-hide'
      },
      statusList () {
        return this.$store.getters.allQuestionStatus
      },
      question () {
        return this.$store.getters.question
      },
      questionCategoryList () {
        console.info(this.$store.getters.allQuestionCategory)
        return this.$store.getters.allQuestionCategory
      },
      levelList () {
        return this.$store.getters.allQuestionLevel
      }
    },
    data () {
      return {
        commentSendContainerStyle: {
          'left': 0
        },
        commentSendSpread: '100%',
        affixOffsetTop: 0,
        questionListLoading: false,
        questionDetailContainerStyle: {
          'width': '60%'
        },
        isSwitch: true
      }
    },
    watch: {
      panelSpread (val) {
        this.updateCommentSendSpread(val)
      }
    },
    methods: {
      updateCommentSendSpread (val) {
        if (!val) {
          this.commentSendSpread = '100%'
        } else {
          this.commentSendSpread = '40%'
        }
      },
      updateQuestionSummary (data) {
        console.info(data)
        this.updateQuestion(data, 'summary')
      },
      updateQuestion (data, field) {

      },
      handlerElChange (val, left) {
        // 打开
        if (val) {
          this.commentSendContainerStyle = {
            'left': 0
          }
        } else {
          this.commentSendContainerStyle = {
            'left': left
          }
        }
      },
      affixChange (val) {
        // 如果不是是固定状态,需计算定值高度
        if (val) {
          this.questionDetailContainerStyle = {
            'width': window.innerWidth * 0.6 + 'px'
          }
        } else {
          this.questionDetailContainerStyle = {
            'width': '60%'
          }
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  @import '../../../../style/base.styl'
  .question-detail-container
    position absolute
    z-index 100
    overflow-x hidden
    overflow-y hidden
    transition right 0.3s ease
    top 0
    right 0
    .question-detail-card
      width 100%
      background-color #fff
      overflow-x hidden
      #question_detail
        overflow-x hidden
        overflow-y scroll
        transform translate(0, 0)
        .question-detail-base
          width 100%
          font-size 16px
          vertical-align middle
          .question-title
            outline none
            background none
            height 36px
            width 100%
            font-size 18px
            border none
            border-radius 6px
            padding 5px
            &:hover
              background-color $-sub-color
          .question-detail-base-2
            margin-bottom 5px
            font-size 12px
            color $-sub-color
          .question-detail-base-3
            font-size 14px
            color $-sub-color
            .question-detail-select
              background none
              border none
              outline none
              font-size 14px !important
            .form-title
              font-size 14px
              color $-sub-color
        .question-summary
          color $-sub-color
          p
            margin-left 10px
        .question-extra
          margin-left 10px
          color $-sub-color
        .question-attachment
          display flex
          justify-content space-between
          margin-left 10px
          color $-sub-color
          font-size 14px

      .ivu-select-selection
        border none !important

  .question-detail-container-spread
    right 0

  .question-detail-container-hide
    right -60%
</style>



