<template>
  <div>
    <Card class="append_question_trend">
      <p slot="title">
        <Icon type="stats-bars">
        </Icon>
        优先级
      </p>
      <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
      <div ref="aqt_echart" class="aqt-echart"></div>
    </Card>
  </div>
</template>
<script>
  import echarts from 'echarts'
  import 'echarts/lib/chart/line'

  export default {
    mounted () {
      this.initCharts()
    },
    beforeDestroy () {
      // 销毁echart
      if (this.chart) {
        this.chart.dispose()
      }
    },
    data () {
      this.$nextTick()
      return {
        chart: '',
        option: {
          color: ['#19be6b', '#f1494e', '#145587'],
          title: {
            text: ''
          },
          tooltip: {
            trigger: 'item',
            axisPointer: {
              animation: false
            }
          },
          legend: {
            left: 'center',
            bottom: '10',
            data: [
              {
                name: '高',
                icon: 'circle'
              },
              {
                name: '中',
                icon: 'circle'
              },
              {
                name: '低',
                icon: 'circle'
              }
            ]
          },
          series: [
            {
              name: '优先级',
              type: 'pie',
              smooth: true,
              radius: ['0', '65%'],
              symbolSize: 1,
              data: [
                {
                  value: 4, name: '高'
                },
                {
                  value: 3, name: '中'
                },
                {
                  value: 2, name: '低'
                }
              ]
            }
          ]
        }
      }
    },
    methods: {
      initCharts () {
        this.chart = echarts.init(this.$refs.aqt_echart)
        this.chart.setOption(this.option)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  .append_question_trend
    height 450px
    position relative
  .aqt-echart
    width 100%
    height 400px
    position absolute
    top 0
    left -30px
</style>



