<template>
  <div class="working-table-container">
    <div class="working-table-category-container">
      <ButtonGroup style="flex:1">
        <Button style="background: #fff">指给我的</Button>
        <Button style="background: #fff">我提出的</Button>
        <Button>我关注的</Button>
      </ButtonGroup>
      <Button type="primary" icon="refresh">刷新</Button>
      </Row>
    </div>


    <!--内容体-->
    <div class="working-table-content-container">
      <div class="working-table-content-item" v-for="(item,index) in questionStatusList" :key="index">
        <WorkList @open-question-detail="open" :name="item.label" @dragchange="dragmove" @dragstart="dragStart" @dragend="dragend">
          <p slot="header">
            <Icon :type="item.icon" :color="item.color"></Icon>
            {{item.label}}(2)
          <p/>
        </WorkList>
      </div>
    </div>
    <QuestionDetail :panelSpread="panelSpread" :uAffixSwitch="uAffixSwitch"></QuestionDetail>
  </div>
</template>

<script>
  import WorkList from './work_list.vue'
  import QuestionDetail from '../question/question_detail.vue'
  import Emitter from '../../../mixin/emitter'

  export default {
    components: {
      WorkList,
      QuestionDetail
    },
    mixins: [Emitter],
    created () {
//      this.$store.dispatch('getAllStatus')
    },
    data () {
      return {
        panelSpread: false,
        uAffixSwitch: false
      }
    },
    computed: {
      questionStatusList () {
        return this.$store.getters.allQuestionStatus
      }
    },
    methods: {
      open () {
        this.panelSpread = !this.panelSpread
        this.uAffixSwitch = !this.uAffixSwitch
      },
      dragStart (params) {
        this.broadcast('workList', 'dragover-notown', params)
      },
      dragend () {
        this.broadcast('workList', 'dragend-notown')
      },
      dragmove () {
        this.broadcast('workList', 'dragmove')
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  @import '../../../../style/base.styl'
  .working-table-container
    position relative
    width 100%
    height 100%
    overflow-y hidden
    overflow-x hidden
    .working-table-category-container
      position relative
      display flex
      justify-content flex-start
      padding 20px 40px
      background-color #fff
    .working-table-content-container
      display flex
      position relative
      padding 10px
      background-color #e2e7eb
      width 100%
      overflow-x scroll
      .working-table-content-item
        flex 0 0 300px
        margin-right 10px
</style>



