<template>
  <div class="question-container">
    <div class="question-list-container" :class="questionListContainerSpread">
      <Row type="flex" justify="start">
        <Col span="6">
        <Card class="question-filter-card" :dis-hover="false">
          <div slot="title" style="height: 32px;line-height: 32px">筛选</div>

          <Form label-position="top">
            <FormItem label="类型" prop="passwd">
              <Input type="password" placeholder="请选择问题类型"/>
            </FormItem>

            <FormItem label="状态" prop="passwd">
              <CheckboxGroup>
                <Row type="flex">
                  <Col span="8">
                  <Checkbox label="twitter">
                    <Icon type="social-twitter" class="question-new"></Icon>
                    <span>新建</span>
                  </Checkbox>
                  </Col>

                  <Col span="8">
                  <Checkbox label="facebook">
                    <Icon type="social-facebook" class="question-processing"></Icon>
                    <span>处理中</span>
                  </Checkbox>
                  </Col>

                  <Col span="8">
                  <Checkbox label="github">
                    <Icon type="social-github" class="question-processed"></Icon>
                    <span>已解决</span>
                  </Checkbox>
                  </Col>

                  <Col span="8">
                  <Checkbox label="snapchat">
                    <Icon type="social-snapchat" class="question-feedback"></Icon>
                    <span>已忽略</span>
                  </Checkbox>
                  </Col>

                  <Col span="8">
                  <Checkbox label="snapchat">
                    <Icon type="social-snapchat" class="question-reopen"></Icon>
                    <span>待反馈</span>
                  </Checkbox>
                  </Col>

                  <Col span="8">
                  <Checkbox label="snapchat">
                    <Icon type="social-snapchat" class="question-closed"></Icon>
                    <span>已关闭</span>
                  </Checkbox>
                  </Col>

                  <Col span="8">
                  <Checkbox label="snapchat">
                    <Icon type="social-snapchat" class="question-unsolved"></Icon>
                    <span>重新打开</span>
                  </Checkbox>
                  </Col>


                </Row>
              </CheckboxGroup>
            </FormItem>


            <FormItem label="优先级" prop="passwd">
              <CheckboxGroup>
                <Row type="flex">
                  <Col span="8">
                  <Checkbox label="twitter">
                    <Icon type="record" class="question-new"></Icon>
                    <span>高</span>
                  </Checkbox>
                  </Col>

                  <Col span="8">
                  <Checkbox label="facebook">
                    <Icon type="record" class="question-processing"></Icon>
                    <span>中</span>
                  </Checkbox>
                  </Col>

                  <Col span="8">
                  <Checkbox label="github">
                    <Icon type="record" class="question-processed"></Icon>
                    <span>低</span>
                  </Checkbox>
                  </Col>
                </Row>
              </CheckboxGroup>


            </FormItem>

            <FormItem label="创建人" prop="passwd">
              <Input type="password" placeholder="请选择问题类型"/>
            </FormItem>


            <FormItem label="指派" prop="passwd">
              <Input type="password" placeholder="请选择问题类型"/>
            </FormItem>

            <FormItem label="创建时间" prop="passwd">
              <DatePicker format="yyyy-MM-dd" type="daterange"
                          placeholder="2016-01-01-2016-02-15" style="width: 100%"></DatePicker>
            </FormItem>

            <FormItem label="最后更新时间" prop="passwd">
              <DatePicker format="yyyy-MM-dd" type="daterange" placeholder="选择更新时间"
                          style="width: 100%"></DatePicker>
            </FormItem>

            <FormItem label="结束时间" prop="passwd">
              <DatePicker format="yyyy-MM-dd" type="daterange" placeholder="选择结束时间"
                          style="width: 100%"></DatePicker>
            </FormItem>

          </Form>
        </Card>
        <div class="card-tip">
          <Icon type="ios-lightbulb-outline"></Icon>
          技巧：筛选条件可以保存起来
        </div>
        </Col>

        <Col span="18">
        <Card class="question-list-card" :dis-hover="false">
          <div slot="title" style="padding: 0 !important;">
            <Input v-model="search" placeholder="搜索问题" style="width: 300px">
            <Button slot="append" icon="ios-search"></Button>
            </Input>
          </div>

          <div slot="extra" class="question-list-card-tool">
            <Tooltip content="刷新">
              <Button type="text" class="btn-divider" size="small">
                <Icon type="refresh" size="14"></Icon>
              </Button>
            </Tooltip>
            <Tooltip content="排序">
              <Button type="text" class="btn-divider" size="small">
                <Icon type="funnel" size="14"></Icon>
              </Button>
            </Tooltip>
            <Tooltip content="显示数量">
              <Button type="text" class="btn-divider" size="small">
                <Icon type="android-menu" size="14"></Icon>
              </Button>
            </Tooltip>
            <Tooltip content="导出内容">
              <Button type="text" class="btn-divider" size="small">
                <Icon type="ios-download" size="14"></Icon>
              </Button>
            </Tooltip>
          </div>
          <div class="question-list" style="height: 100%">
            <Table style="height: 100%" :columns="columns1" :data="data2" :show-header="false" :highlight-row="true"
                   :stripe="true" @on-row-click="questionSelect" :loading="questionListLoading"></Table>
          </div>
        </Card>
        <div class="card-tip">
          <Row type="flex" justify="end">
            <Page :total="100" size="small" show-total></Page>
          </Row>
        </div>
        </Col>
      </Row>
    </div>
    <!--问题详情 -->
    <QuestionDetail :panelSpread="panelSpread" :uAffixSwitch="uAffixSwitch"></QuestionDetail>
  </div>
</template>

<script>
  import QuestionDetail from '../../project_detail/question/question_detail.vue'
  var levelColors = ['#5bc0de', '#ffbc00', '#45be95']
  export default {
    components: {
      QuestionDetail
    },
    data () {
      return {
        search: '',
        panelSpread: false,
        uAffixSwitch: false,
        questionListLoading: false,
        columns1: [
          {
            title: '优先级',
            key: 'level',
            width: 50,
            render: (h, params) => {
              let level = params.row.level
              let colors = levelColors.filter((color, index) => { return index === level })
              return h('Icon', {
                props: {type: 'record', color: colors[0]}
              }, '')
            }
          },
          {
            title: '编号',
            key: 'id',
            width: 100,
            render: (h, params) => {
              return h('strong', {
                'class': {'app-color': true, 'base-font-size': true},
                style: {'font-size': '14px'}
              }, '#' + params.row.id)
            }
          },
          {
            title: '内容',
            key: 'content',
            render: (h, params) => {
              let category = params.row.category
              let tagColor = 'default'
              let tagName = ''
              if (category === 0) {
                tagColor = 'blue'
                tagName = '功能'
              } else if (category === 1) {
                tagColor = 'green'
                tagName = '任务'
              } else {
                tagColor = 'red'
                tagName = 'bug'
              }
              return h('div', [
                h('p', {
                  'class': {'app-color': true, 'base-font-size': true},
                  style: {'margin-bottom': '10px', 'margin-top': '10px'}
                }, params.row.title),
                h('Row', {props: {type: 'flex', justify: 'start'}}, [
                  h('Col', {props: {span: 3}}, [
                    h('Tooltip', {props: {content: '问题类型', transfer: true, placement: 'top'}}, [
                      h('Tag', {
                        props: {color: tagColor},
                        'class': {'sub-color': true},
                        style: {'margin-bottom': '-10px'}
                      }, tagName)
                    ])
                  ]),
                  h('Col', {props: {span: 3}}, [
                    h('Tooltip', {props: {content: '创建人', transfer: true, placement: 'top'}}, [
                      h('Button', {
                        props: {type: 'text', icon: 'ios-person'},
                        'class': {'base-font-size': true, 'sub-color': true}
                      }, params.row.author)
                    ])
                  ]),
                  h('Col', {props: {span: 3}}, [
                    h('Tooltip', {props: {content: '指派人', transfer: true, placement: 'top'}}, [
                      h('Button', {
                        props: {type: 'text', icon: 'speakerphone'},
                        'class': {'base-font-size': true, 'sub-color': true}
                      }, params.row.assign === 0 ? '未指派' : params.row.assigner)
                    ])
                  ]),
                  h('Col', {props: {span: 3}}, [
                    h('Tooltip', {props: {content: '更新时间', transfer: true, placement: 'top'}}, [
                      h('Button', {
                        props: {type: 'text', icon: 'eye'},
                        'class': {'base-font-size': true, 'sub-color': true}
                      }, params.row.updateAt)
                    ])
                  ])
                ])
              ], '')
            }
          }
        ],
        data2: [
          {
            level: 0,
            id: '018',
            category: 0,
            title: '每一个问题的下方都可以新建子问题，将每个问题拆分来处理。',
            author: 'zhoulin',
            assign: 0,
            assigner: '',
            updateAt: '2017-11-28 19:46',
            status: 0
          },
          {
            level: 0,
            id: '018',
            title: '每一个问题的下方都可以新建子问题，将每个问题拆分来处理。',
            author: 'zhoulin',
            assign: 1,
            assigner: 'zhoulin',
            updateAt: '2017-11-28 19:46',
            status: 0
          }]
      }
    },
    methods: {
      questionSelect () {
        this.open()
      },
      open () {
        this.panelSpread = !this.panelSpread
        this.uAffixSwitch = !this.uAffixSwitch
      }
    },
    computed: {
      questionListContainerSpread () {
        return this.panelSpread ? 'question-list-container-spread' : 'question-list-container-hide'
      }
    },
    mounted () {
//      this.$refsxx.questionDetail.$emit('el-change', false, '60%')
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  @import "../../../../style/base.styl"
  @import "../../../../style/project/project.styl"
  .question-container
    display flex
    width 100%
    position relative
    overflow-x hidden
    .question-list-container
      position relative
      z-index 9
      width 100%
      padding 20px 20px
      transition left 0.3s ease
      overflow-x hidden
      overflow-y scroll
      .question-filter-card
      .question-list-card
        height 95%
        overflow-y scroll
        position relative
        .question-list-card-tool
          position relative
          display flex
          left 0
  .btn-divider
    border-right 1px solid $-hr-color
    border-radius 0
    width 48px

  .question-list-container-spread
    left -40%

  .question-list-container-hide
    left 0

</style>



