<template>
  <div>
    <Card class="append_question_trend">
      <p slot="title">
        <Icon type="stats-bars">
        </Icon>
        人员及问题分布
      </p>
      <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
      <div ref="aqt_echart" class="aqt-echart"></div>
    </Card>
  </div>
</template>

<script>
  import echarts from 'echarts'
  import 'echarts/lib/chart/line'

  export default {
    mounted () {
      this.initCharts()
    },
    beforeDestroy () {
      // 销毁echart
      if (this.chart) {
        this.chart.dispose()
      }
    },
    data () {
      return {
        chart: '',
        option: {
          color: ['#19be6b', '#f1494e', '#145587'],
          title: {
            text: ''
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              animation: false
            }
          },
          legend: {
            data: [
              {
                name: '功能',
                icon: 'circle'
              },
              {
                name: '任务',
                icon: 'circle'
              },
              {
                name: 'bug',
                icon: 'circle'
              }
            ]
          },
          xAxis: {
            axisLabel: {
              interval: 0,
              rotate: 0
            },
            data: ['zhoulin', '周晟成', '可笑的霸王', '可笑的虞姬', '可笑的刘邦']
          },
          yAxis: {
            minInterval: 5,
            type: 'value'
          },
          series: [
            {
              name: '功能',
              type: 'bar',
              smooth: true,
              symbolSize: 1,
              areaStyle: {normal: {}},
              data: [1, 2, 3, 4, 1]
            },
            {
              name: '任务',
              type: 'bar',
              smooth: true,
              symbolSize: 1,
              areaStyle: {normal: {}},
              data: [2, 2, 2, 4, 1]
            },
            {
              name: 'bug',
              type: 'bar',
              smooth: true,
              symbolSize: 1,
              areaStyle: {normal: {}},
              data: [3, 1, 3, 3, 1]
            }
          ]
        }
      }
    },
    methods: {
      initCharts () {
        this.chart = echarts.init(this.$refs.aqt_echart)
        this.chart.setOption(this.option)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  .append_question_trend
    height 300px
    position relative

  .aqt-echart
    width 100%
    height 250px
    position absolute
    top 50px
    left -30px
</style>



