<template>
  <div>
    <Card class="question-sta">
      <p slot="title">
        <Icon type="document-text">
        </Icon>
        问题
      </p>

      <div class="question-sta-content">
        <Row type="flex" :justify="justify" :gutter="12">
          <Col :span="column" class="question-sta-list-item">
            <p class="question-sta-list-item-num question-new">0</p>
            <p class="question-sta-list-item-tag">新建</p>
          </Col>

          <Col :span="column" class="question-sta-list-item">
            <p class="question-sta-list-item-num question-processing">0</p>
            <p class="question-sta-list-item-tag">处理中</p>
          </Col>

          <Col :span="column" class="question-sta-list-item">
            <p class="question-sta-list-item-num question-processed">0</p>
            <p class="question-sta-list-item-tag">已解决</p>
          </Col>
          <br>
          <Col :span="column" class="question-sta-list-item">
            <p class="question-sta-list-item-num question-feedback">1</p>
            <p class="question-sta-list-item-tag">已忽略</p>
          </Col>

          <Col :span="column" class="question-sta-list-item">
            <p class="question-sta-list-item-num question-reopen">0</p>
            <p class="question-sta-list-item-tag">待反馈</p>
          </Col>

          <Col :span="column" class="question-sta-list-item">
            <p class="question-sta-list-item-num question-closed">0</p>
            <p class="question-sta-list-item-tag">已关闭</p>
          </Col>
          <br>
          <Col :span="column" class="question-sta-list-item">
            <p class="question-sta-list-item-num question-unsolved">0</p>
            <p class="question-sta-list-item-tag">重新打开</p>
          </Col>

        </Row>
      </div>
    </Card>
  </div>
</template>

<script>
  export default {
    props: {
      col: {
        type: Number,
        default: 3
      },
      justify: {
        type: String,
        default: 'start',
        validator (val) {
          return val === 'start' || val === 'center' || val === 'end'
        }
      }
    },
    data () {
      return {
        column: parseInt(24 / this.col)
      }
    },
    mounted () {
      console.info(this.col)
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  @import "../../../../style/project/project.styl"
  .question-sta
    .question-sta-content
      height 100%
      overflow scroll
      padding 20px 10px
      .question-sta-list
        display flex
        flex-wrap wrap
        flex-direction row
      .question-sta-list-item
        text-align center
        margin-top 10px
        .question-sta-list-item-num
          font-size 36px
          cursor pointer
        .question-sta-list-item-tag
          color #777
</style>



