<template>
  <div id="projects">
    <transition name="fade">
      <!--<Loading ref="loading" v-show="false"></Loading>-->
      <div class="project-container">
        <!--头部-->
        <div class="head">
          <Row type="flex" align="middle" justify="start">
            <Col span="12">
            <Row type="flex" justify="start" align="middle">
              <Col span="8">
              <Logo page="" :height="60"></Logo>
              </Col>
              <Col span="10">
              <Search v-model="search" placeholder="搜索项目"></Search>
              </Col>
            </Row>
            </Col>
            <Col span="3" :offset="8">
            <Row type="flex" justify="end" align="middle">
              <Dropdown>
                <a class="user-info">
                  <Avatar src="/static/img/avatar/avatar1.JPG"></Avatar>
                  <strong class="username">{{username}}</strong>
                  <Icon type="arrow-down-b" size="small" color="#fff"></Icon>
                </a>
                <!--<Dropdown-menu slot="list" >-->
                <!--<Dropdown-item>Signed in as-->
                <!--<span>{{username}}</span></Dropdown-item>-->
                <!--<Dropdown-item ><Button @click="logout" type="text">注销</Button></Dropdown-item>-->
                <!--</Dropdown-menu>-->
              </Dropdown>
            </Row>
            </Col>
          </Row>
        </div>
        <!--内容 -->
        <router-view></router-view>
      </div>
    </transition>
  </div>
</template>
<script>
  import Loading from '../common/loading.vue'
  import Search from '../common/search.vue'
  import Logo from '../common/logo.vue'

  export default {
    created () {
      this.init()
    },
    data () {
      return {
        show: true,
        search: '我爱的人',
        username: 'zhoulin'
      }
    },
    computed: {
      loading () {
        return this.$store.getters.projectLoading
      }
    },
    watch: {
      search (val) {
        console.info(val)
      }
    },
    components: {
      Loading,
      Search,
      Logo
    },
    methods: {
      init () {
        this.$store.dispatch('getAllCreatedProject')
      }
    }
  }
</script>
<style scoped type="text/stylus" lang="stylus">
  @import "../../style/base.styl"
  #projects
    width 100%
    height 100%
    overflow-y hidden

  .demo-spin-col
    height: 100px;
    position: relative;
    border: 1px solid #eee;

  .loading
    width: 100%;
    height: 100%;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;

  .project-container
    margin-bottom 30px
    height 100%
    .head
      height: 60px;
      width: 100%;
      background-color: $-app-color;
      .user-info
        .username
          color #fff
          margin-left 3px
          margin-right 3px
</style>



