<template>
  <Card class="setting-card">
    <p slot="title">
      <Icon type="document-text"></Icon>
      模块管理
    </p>

    <Table :columns="columns" :data="datas">

    </Table>


    <p slot="extra" class="card-extra">
      <Button type="primary" icon="ios-people" size="small">创建模块</Button>
    </p>
  </Card>
</template>

<script>
  export default {
    data () {
      return {
        columns: [
          {
            title: '模块名称',
            key: 'name',
            width: '50%'
          },
          {
            title: '问题数',
            key: 'questionNum',
            width: '40%'
          },
          {
            title: ' ',
            render: (h, params) => {
              return h('div',
                {class: 'hover-show', style: {'width': '100%'}}, [h('ButtonGroup', {
                  props: {
                    size: 'small'
                  }
                }, [
                  h('Button', {
                    props: {
                      type: 'text',
                      icon: 'edit'
                    }
                  }),
                  h('Button', {
                    props: {
                      type: 'text',
                      icon: 'trash-b'
                    }
                  })
                ])])
            }
          }
        ],
        datas: [
          {
            name: '管理员',
            questionNum: 1
          },
          {
            name: '开发人员',
            questionNum: 0
          },
          {
            name: '测试人员',
            questionNum: 0
          }]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  @import "../../../../style/project/project.styl"
</style>



