<template>
  <div class="work-list-container">
    <div class="work-list-title">
      <slot name="header">
        <Icon type="document-text"></Icon>
        新建(1)
      </slot>
    </div>
    <!---->
    <ul class="work-list" ref="workList">
      <a @click="open" v-for="(item,index) in questions" :key="index"
      >
        <li class="work-list-item" draggable="true" :class="{'work-list-item-drag': item.isDrag}">
          <a class="work-title">
            {{item.title}}
          </a>
          <div class="work-time">
            <Icon type="document-text" size="12"></Icon>
            <span>{{item.createTime}}</span>
          </div>
          <ul class="work-list-content">
            <li>
              <Tag type="border" color="#e4e5e7">#{{item.id}}</Tag>
            </li>
            <li>
              <Tag color="blue">{{item.category}}</Tag>
            </li>
            <li>
              <Icon type="document-text" size="12"></Icon>
              <span class="ellipsis-text">{{item.author}}</span>
            </li>
          </ul>
        </li>
      </a>
    </ul>
  </div>
</template>
<script>
  export default {
    props: {
      name: {
        default: Math.random()
      }
    },
    components: {},
    name: 'workList',
    data () {
      return {
        dragElement: '',
        dragIndex: -1,
        isDragEnter: false,
        questions: [
          {
            id: '001',
            category: '功能',
            title: '功能标题',
            author: 'zhoulin',
            createTime: '2012-12-93 13:123'

          },
          {
            id: '002',
            category: '任务',
            title: '任务标题',
            author: 'zhoulin',
            createTime: '2012-12-93 13:123'

          },
          {
            id: '003',
            category: 'bug',
            title: 'bug标题',
            author: 'zhoulin',
            createTime: '2012-12-93 13:123'
          }
        ]
      }
    },
    methods: {
      open () {
        this.$emit('open-question-detail', true)
      },
      isDragMeAndInside () {
        return this.dragElement && this.dragElement.name === this.name
      },
      isDragCancel () {
        return this.dragElement && (this.dragElement.lastOverName === this.name || !this.dragElement.isLayUp)
      },
      isFirstEnter () {
        return this.dragElement && this.dragElement.lastOverName !== this.name
      },
      lastDragEnterIsMe () {
        return this.dragElement && this.dragElement.lastOverName === this.name
      },
      isUp (index) {
        return index === this.questions.length - 1
      },
      initDragEvents () {
        // 注册拖拽事件
        let dragItems = Array.from(this.$refs.workList.getElementsByClassName('work-list-item'))
        console.info(dragItems.length)
        dragItems.forEach((item, index) => {
          let i = index
          // 拖拽开始
          item.ondragstart = () => {
            console.log(this.questions.length, this.dragIndex, i)
            this.dragElement = {
              'name': this.name,
              'lastOverName': this.name,
              'data': this.questions[i],
              'dataIndex': i,
              'isLayUp': false,
              'dragEnterIndex': this.dataIndex
            }
            this.questions[i].isDrag = true
            this.$set(this.questions, i, this.questions[i])
            this.$emit('dragstart', this.dragElement)
          }
          item.ondragend = () => {
            if (this.isDragCancel()) {
              this.questions[this.dragElement.dragEnterIndex].isDrag = false
              this.$set(this.questions, this.dragElement.dragEnterIndex, this.questions[this.dragElement.dragEnterIndex])
            } else if (this.dragElement.isLayUp) {
              // remove
              let questions = this.questions
              questions.splice(this.dragElement.dataIndex, 1)
              this.questions = questions
              this.$emit('dragend', this.dragElement)
            }
            this.$nextTick(() => {
              this.initDragEvents()
            })
          }
          item.ondragenter = () => {
            // 通知其余组件隐藏拖拽
            if (!this.lastDragEnterIsMe() && this.isFirstEnter()) {
              this.$emit('dragchange')
            }
            if (this.isDragMeAndInside() || !this.isFirstEnter()) {
              if (this.isDragMeAndInside()) {
                this.dragElement.dragEnterIndex = this.dragElement.dataIndex
                this.dragElement.dataIndex = i
              }
              if (this.dragElement.dragEnterIndex !== i) {
                let questions = this.questions
                questions.splice(this.dragElement.dragEnterIndex, 1)
                questions.splice(i, 0, this.dragElement.data)
                this.questions = questions
              }
            } else {
              let questions = this.questions
              questions.splice(i, 0, this.dragElement.data)
              this.questions = questions
              this.dragElement.isLayUp = true
              this.$nextTick(() => {
                this.initDragEvents()
              })
            }
            this.dragElement.dragEnterIndex = i
            this.dragElement.lastOverName = this.name
            this.$emit('dragstart', this.dragElement)
          }
          // 拖拽结束
          item.ondragover = () => {
            event.preventDefault()
          }
          item.ondragleave = () => {
            if (i === this.questions.length - 1) {
              console.info('末尾')
            }
          }
        })
      }
    },
    mounted () {
      // 注册其他组件经过本组件事件
      this.$on('dragover-notown', (params) => {
        this.dragElement = params
      })
      // 注册其他组件结束拖拽事件
      this.$on('dragend-notown', (params) => {
        if (this.dragElement.lastOverName === this.name) {
          this.questions[this.dragElement.dragEnterIndex].isDrag = false
          this.$set(this.questions, this.dragElement.dragEnterIndex, this.questions[this.dragElement.dragEnterIndex])
        }
      })

      // 当拖拽置其他组件触发事件
      this.$on('dragmove', () => {
        if (this.dragElement.lastOverName === this.name && this.dragElement.name !== this.name) {
          console.info('123123123')
          let questions = this.questions
          questions.splice(this.dragElement.dragEnterIndex, 1)
          this.questions = questions
        }
      })
      this.questions.forEach(item => {
        item.isDrag = false
      })
      this.$nextTick(() => {
        this.initDragEvents()
      })
      // 注册列表拖拽事件
      this.$refs.workList.ondragenter = (event) => {
        event.preventDefault()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  .work-list-container
    position relative
    width 100%
    height 100%
    background-color #fff
    .work-list-title
      padding 10px
      background-color #fff
      border-bottom 1px solid #eee
    .work-list
      position relative
      margin-top 10px
      padding 0 10px 0 10px
      height 500px
      width 100%
      overflow-y scroll
      a
        display block
        position relative
      .work-list-item
        display block
        margin-bottom 10px
        padding 20px 10px
        border-left 5px solid #f8ac59
        border-top 1px solid #eee
        border-bottom 1px solid #eee
        border-right 1px solid #eee
        border-radius 3px
        background-color #fff
        .work-list-content
          display flex
          align-items center
          margin-top 10px
          li
            margin-right 10px
            font-size 12px
            color #b2b2b2
            .ellipsis-text
              display inline-block
              overflow: hidden;
              vertical-align top
        .work-time
          margin-top 10px
          text-align left
          font-size 12px
          color #b2b2b2

  .work-list-item-drag
    border 1px dashed #b2b2b2 !important
</style>



