<template>
  <div class="member-invite">
    <Tabs value="通过邮箱邀请">
      <TabPane label="通过邮箱邀请" name="name1">标签一的内容</TabPane>
      <TabPane label="通过链接邀请" name="name2">
        <div class="url-way">
          <span class="url-way-tip">访问此链接的用户可直接加入项目，链接的有效期是一天。</span>
          <Input v-model="inviteUrl" size="large" ref="inviteUrlInput" disabled>
          <span slot="prepend">邀请链接</span>
          <Button slot="append" @click="coverUrl" class="cover-url-btn" v-show="!isCover">复制链接</Button>
          <Button slot="append" @click="coverUrl" class="cover-url-btn" v-show="isCover">已复制</Button>
          </Input>
        </div>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        inviteUrl: 'https://www.tracup.com/user/invite/39eeedc6a9946217595204d64b6589b0',
        isCover: false
      }
    },
    methods: {
      coverUrl () {
        // 执行复制事件
        this.$refs.inviteUrlInput.$refs.input.select()
        document.execCommand('copy')
        this.$Notice.success({
          title: '链接复制成功'
        })
        if (!this.isCover) {
          this.isCover = true
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  @import "../../../../style/base.styl"
  .member-invite
    .url-way
      height 100%
      padding 20px
      display flex
      flex-direction column
      justify-content center
      align-items flex-start
      .url-way-tip
        margin-bottom 10px
        color $-sub-color
        font-size 14px
</style>



